body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* PianoDarkTheme */

.PianoDarkTheme .ReactPiano__Key--accidental {
  background: #025d7d;
  border: 1px solid #888;
}
.PianoDarkTheme .ReactPiano__Key--natural {
  background: #013243;
  border: 1px solid #888;
  margin-right: 0;
}
.PianoDarkTheme .ReactPiano__Key--active.ReactPiano__Key--accidental {
  background: #0396ca;
}
.PianoDarkTheme .ReactPiano__Key--active.ReactPiano__Key--natural {
  background: #0396ca;
}

.glyphicon-small {
  font-size: 1.2em;
}

.glyphicon-medium {
  font-size: 2em;
}

.glyphicon-big {
  font-size: 4em;
}

@media screen and (max-width: 299px) {
  .glyphicon {
    font-size: 1.2em;
  }
}

@media screen and (min-width: 300px) and (max-width: 799px) {
  .glyphicon {
    font-size: 2em;
  }
}

@media screen and (min-width: 800px) {
  .glyphicon {
    font-size: 4em;
  }
}
